@import "_variables";

/* The side navigation menu */
#menu {
  height: 100%;
  width: 40vw;
  position: fixed;
  z-index: 200;
  top: 0;
  left: 100%;
  background-color: white;
  overflow-x: hidden;

  transition: 0.5s;

  text-transform: uppercase;

  overflow: hidden;
  padding: $margin-width;
}

#menu-list-container {
  // padding-top: 3rem;

.row {
  margin-bottom: 1.5rem;
}
}

#menu a {
  // padding: 8px 8px 8px 32px;
  text-decoration: none;
  // font-size: 25px;
  color: black;
  // display: block;
  // transition: 0.3s;
}

/* When you mouse over the navigation links, change their color */
#menu a:hover {
  // color: #f1f1f1;
}

/* Position and style the close button (top right corner) */
#menu .closebtn {
  cursor: pointer;
  line-height: 0.2;
}


